<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基本用法1">基本用法</a></li><li><a href="#vue-doc-样式类型1">样式类型</a></li><li><a href="#vue-doc-各种颜色1">各种颜色</a></li><li><a href="#vue-doc-可选择1">可选择</a></li><li><a href="#vue-doc-动态添加和删除1">动态添加和删除</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-zhcntag标签enusbutton1" class="vue-doc-element">zh-CNTag 标签 en-USButton</h1>
<p class="vue-doc-element">zh-CN对不同维度进行简单的标记和分类。 en-USBasic component that is used to trigger bussiness logic.</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-zhcntag标签enusbutton1" class="vue-doc-element">zh-CNTag 标签 en-USButton</h1>
<p class="vue-doc-element">zh-CN对不同维度进行简单的标记和分类。 en-USBasic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demostyle></demostyle>
<democolors></democolors>
<democheckable></democheckable>
<demoaddAndRemove></demoaddAndRemove>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-tagprops1_1" class="vue-doc-element">Tag props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>closable</td>
<td>标签是否可以关闭</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>checkable</td>
<td>标签是否可以选择</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>checked</td>
<td>标签的选中状态</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>type</td>
<td>标签的样式类型，可选值为 border、dot或不填</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>color</td>
<td>标签颜色，预设颜色值为blue、green、red、yellow、default，你也可以自定义颜色值。</td>
<td>String</td>
<td>—</td>
</tr>
<tr>
<td>name</td>
<td>当前标签的名称，使用 v-for，并支持关闭时，会比较有用</td>
<td>String / Number</td>
<td>-</td>
</tr>
<tr>
<td>fade</td>
<td>是否在出现和消失时使用渐变的动画，动画时长可能会引起占位的闪烁</td>
<td>Boolean</td>
<td>true</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-tagevents1_2" class="vue-doc-element">Tag events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th>Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demostyle from './demo/style.md';
import democolors from './demo/colors.md';
import democheckable from './demo/checkable.md';
import demoaddAndRemove from './demo/addAndRemove.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demostyle,democolors,democheckable,demoaddAndRemove
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


